@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
body {
  display: table;
  -webkit-align-items: center;
          align-items: center;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* { 
  box-sizing: border-box; 
}

/* scroll trick 

body {
	scroll-snap-type: mandatory;
	scroll-snap-points-y: repeat(100vh);
	scroll-snap-type: y mandatory;
}

section {
  height: 100vh;
	scroll-snap-align: start;
	text-align: center;
}

*/

body {
  font-size: calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
  overflow: hidden;
}

.about-image {
  position: relative;
  display: table-cell;
  width: 50%;
  height: 100vh;
  background-size: cover;
  background-position: 50% 20%;
  float: left;
}

.about-text {
  font-family: 'Roboto Slab', serif;
  vertical-align: middle;
  display: table-cell;
  width: 50%;
  -webkit-align-items: center;
          align-items: center;
}

h1, h2, h3 {
  margin-top: 20px;
  margin-bottom: 10px;
  text-align: center;
  word-break: keep-all;
}

.pull-left  {
  text-align: left;
}

.header  {
  width: 100vw;
  display: table-cell;
  padding: 10px;
  margin: -10px;
}

span {
  display: inherit;
}

.footer {
  display: table;
  height: 100px;
  width: 100%;
  text-align: center;
}

.footer span {
  display: table-cell;
  vertical-align: middle;
}

.footer a { 
  text-decoration: none;
}

.footer a:link {
  color: inherit;
}

.footer a:visited {
  color: inherit;
}

.footer a:hover { 
  text-decoration: none;
  color: lightblue;
}

.footer span {
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
  -webkit-transform: scale(1);
	transform: scale(1);
	-webkit-transition: .2s ease-in-out;
	transition: .2s ease-in-out;
}

.footer span:hover {
	-webkit-filter: grayscale(0);
  filter: grayscale(0);
  -webkit-transform: scale(1.2);
	transform: scale(1.2);
}

/* On hover, add a black background color with a little bit see-through  */
.about-image:hover, .about-image:hover {
  cursor: pointer;
  box-shadow: 0 0 11px rgba(33,33,33,.2); 
}

#id1  {
  background-image: url(/static/media/1.80ba3082.jpg);
}

#id2 {
  background-image: url(/static/media/2.fd3c96ae.jpg);
  background-position: 80% 50%;
}

#id3 {
  background-image: url(/static/media/3.b0b93acd.gif);
  background-position: 73% 50%;
}

#cats {
  padding: 10px;
  text-align: center;
  height: 60px;
  border-radius: 15px;
}

/* Caption text  */
.caption-text {
  color: #f2f2f2;
  font-size:  16px;
  padding: 8px 15px;
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 100%;
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}

.scrolling {
  overflow: auto;
  height: 100vh;
}

@media screen and (max-width: 900px)  {

  body {
    overflow-x: auto;
    overflow-y: hidden;
    box-sizing: border-box; 
  }
  .about-image {
    display: block;
    width: 100%;
    height: 450px;
    background-size: cover;
    background-position: 50% 40%;
    float:left;
    -webkit-margin-after: 5%;
            margin-block-end: 5%;
  }

  .about-text {
    display: block;
    padding: 20px;
    -webkit-align-items: center;
            align-items: center;
  }
  .scrolling {
    overflow: inherit;
  }
}

.hidden
{
  display: none;
}
